<template>
  <div class="shopSafe_container">
      <heade-top head-title="食品监督安全公示" go-back="true"></heade-top>
      <section class="shopSafe_main">
          <section class="shopSafe_main_container">
             <header>食品监督安全公示</header>
                <section class="img_rout">
                    <div>
                        <svg class="shop_status" v-if="shopDetail.status == 1">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#res-well"></use>
                        </svg>
                            <svg class="res-well" v-else>
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#res-bad"></use>
                            </svg>
                    </div>
                    <div class="jianca_tx">
                        <p>
                            <span>监督检查结果:</span>
                            <span v-if="shopDetail.status==1" class="Success">良好</span>
                            <span v-else class="Error">差</span>
                        </p>
                        <p>
                            <span>检查日期:</span>
                            <span>{{shopDetail.identification.identificate_date && shopDetail.identification.identificate_date.split('T')[0]}}</span>
                        </p>
                    </div>
                </section>
          </section>
      </section>
      <section class="shopSafe_gongshang">
          <header>工商登记信息</header>
          <ul>
             <li>
                <p>企业名称</p>
                <p>{{shopDetail.identification.company_name}}</p>
            </li>
            <li>
                <p>工商执照注册号</p>
                <p>{{shopDetail.identification.identificate_agency}}</p>
            </li>
            <li>
                <p>注册资本</p>
                <p>{{shopDetail.identification.registered_number}}</p>
            </li>
            <li>
                <p>注册地址</p>
                <p>{{shopDetail.identification.registered_address}}</p>
            </li>
            <li>
                <p>属地监管所</p>
                <p>{{shopDetail.identification.registered_principal}}</p>
            </li>
            <li>
                <p>法定代表人</p>
                <p>{{shopDetail.identification.legal_person}}</p>
            </li>
            <li>
                <p>经营范围</p>
                <p>{{shopDetail.identification.operation_period}}</p>
            </li>
        </ul>
      </section>
      <section class="shopSafe_fuke">
          <header>餐饮许可证</header>
            <ul>
                <li>
                    <p>营业范围</p>
                    <p>{{shopDetail.identification.operation_period}}</p>
                </li>
                <li>
                    <p>许可证有效期</p>
                    <p>{{shopDetail.identification.licenses_date}}</p>
                </li>
                <li>
                    <p>许可证号</p>
                    <p>{{shopDetail.identification.licenses_number}}</p>
                </li>
                <li>
                    <p>发证时间</p>
                    <p>{{shopDetail.identification.licenses_scope}}</p>
                </li>
                <li>
                    <p>发证机关</p>
                    <p>{{shopDetail.identification.registered_principal}}</p>
                </li>
            </ul>
      </section>
  </div>
</template>

<script>
import headeTop from "../../../../components/header/header";
import { mapState } from "vuex";
import { imageBaseUrl } from "../../../../config/env";
import BScroll from "better-scroll";
import { getImgPath } from "../../../../components/common/mixin";
export default {
  components: {
    headeTop
  },
  data() {
    return {
      imageBaseUrl
    };
  },
  mixins: [getImgPath],
  computed: {
    ...mapState(["shopDetail"])
  },
  mounted() {
    this.$nextTick(() => {
      new BScroll("#scroll_section", {
        deceleration: 0.001,
        bounce: true,
        swipeTime: 1800,
        click: true
      });
    });
  }
};
</script>

<style lang="scss" scoped>
@import "../../../../style/mixin.scss";
.shopSafe_container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 102;
  background: #ededed;
  padding-top: 1.95rem;
}
.img_rout {
  display: flex;
  padding: 0.6rem;
  svg {
    @include wh(2rem,2rem);
  }
  .jianca_tx {
    p {
      padding-bottom: 0.5rem;
      padding-left: 0.5rem;
    }
    p:nth-of-type(1) {
      @include sc(0.5rem,#666);
      .Success {
        color: rgb(126, 211, 33);
        padding-left: 0.3rem;
      }
      .Error {
        color: red;
        padding-left: 0.3rem;
      }
    }
    p:nth-of-type(2) {
      @include sc(0.5rem,#666);
    }
  }
}
.shopSafe_main {
  display: flex;
  justify-content: columns;
  .shopSafe_main_container {
    background: #fff;
    width: 100%;
    header {
      padding: 0.6rem;
      @include sc(0.7rem,#555);
      font-weight: bold;
      border-bottom: 0.025rem solid #f1f1f1;
    }
  }
}
.shopSafe_gongshang {
  margin-top: 0.6rem;
  background: #fff;
  width: 100%;
  header {
    padding: 0.6rem;
    @include sc(0.6rem,#777);
    font-weight: bold;
    border-bottom: 0.025rem solid $bc;
  }
  ul {
    padding: 0.5rem;
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 0.8rem;
      p {
        @include sc(0.5rem,#666);
      }
    }
  }
}
.shopSafe_fuke {
  margin-top: 0.6rem;
  background: #fff;
  header {
    @include sc(0.7rem,#666);
    font-weight: bold;
    padding: 0.6rem;
    border-bottom: 0.025rem solid $bc;
  }
   ul {
    padding: 0.5rem;
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 0.8rem;
      p {
        @include sc(0.5rem,#666);
      }
    }
  }
}
</style>



